import React, { useState } from "react";
import "./Additionally.scss";
import { Radio } from "../../../components/Radio";
import { SelectField } from "../../../components/SelectField";

const COLOR_TYPES = {
  ALL: "all",
  RED: "red",
  BLUE: "blue",
};

const Additionally = () => {
  const [carType, setCarType] = useState(COLOR_TYPES.ALL);

  const onColorTypeChange = (event) => {
    setCarType(event.target.value);
  };

  return (
    <div className="additionally">
      <div className="additionally__color">
        <p>Цвет</p>
        <div className="additionally__color-radio">
          <Radio
            name="Любой"
            value={COLOR_TYPES.ALL}
            onChange={onColorTypeChange}
            checked={carType === COLOR_TYPES.ALL}
          />
          <Radio
            name="Красный"
            value={COLOR_TYPES.RED}
            onChange={onColorTypeChange}
            checked={carType === COLOR_TYPES.RED}
          />
          <Radio
            name="Голубой"
            value={COLOR_TYPES.BLUE}
            onChange={onColorTypeChange}
            checked={carType === COLOR_TYPES.BLUE}
          />
        </div>
      </div>
      <div className="additionally__date">
        <p>Дата аренды</p>
        <div className="additionally__date-select">
          <SelectField name="С" />
          <SelectField name="По" />
        </div>
      </div>
      <div className="additionally__rate">
        <p>Тариф</p>
        <div className="additionally__rate-radio">
          <Radio
            name="Поминутно, 7₽/мин"
            value={COLOR_TYPES.ALL}
            onChange={onColorTypeChange}
            checked={carType === COLOR_TYPES.ALL}
          />
          <Radio
            name="На сутки, 1999 ₽/сутки"
            value={COLOR_TYPES.RED}
            onChange={onColorTypeChange}
            checked={carType === COLOR_TYPES.RED}
          />
        </div>
      </div>
      <div className="additionally__services">
        <p>Доп услуги</p>
      </div>
    </div>
  );
};

export default Additionally;
